import React, { useState, useContext } from 'react'
import {navigate} from 'gatsby'
import reduce from 'lodash/reduce'
import PropTypes from 'prop-types'
import downArrow from './down-arrow.png'
import whiteBag from './white-bag.svg'
import blackBag from './black-bag.svg'
import SearchResult from './searchResult'
import SearchResultLarge from './searchResultLarge'
import styled from '@emotion/styled'
import {
  Grid
} from '../ProductGrid/styles'

import algoliasearch from 'algoliasearch/lite';
import { InstantSearch, SearchBox, Hits } from 'react-instantsearch-dom';

import StoreContext from '~/context/StoreContext'
import {
	CartCounter, 
	Container,
	Logo,
	MenuLink,
	Wrapper
} from './styles'

const SearchResultsWrapper = styled.div`
  margin: 0 auto;
  max-width: 1020px;
  padding: 0px 0rem 1.45rem;
`

const searchClient = algoliasearch('MFHEG5ET0Q', '135d9adeac6cd24e0cfd522e093d7301');

const useQuantity = () => {
	const { store: {checkout} } = useContext(StoreContext)
	const items = checkout ? checkout.lineItems : []
	const total = reduce(items, (acc, item) => acc + item.quantity, 0)
	return [total !== 0, total]
}

const Navigation = ({ siteTitle, location }) => {
  const [hasItems, quantity] = useQuantity()
  const [searchTerm, setSearchTerm] = useState("")
  const [searchActive, setSearchActive] = useState(false)
  const [searchPageVisible, setSearchPageVisible] = useState(false)

  function handleFocus(e) {
  	e.target.value.length >= 1 && setSearchActive(true)
  }

  function handleBlur() {
  	setTimeout(() => setSearchActive(false), 170)
  }

  function handleChange(e) {
  	e.target.value.length >= 1 ? setSearchActive(true) : setSearchActive(false)
  	e.target.value.length >= 1 && setSearchPageVisible(true)
  	e.target.value.length === 0 && setSearchPageVisible(false)
  	setSearchTerm(e.target.value)
  }

  function handleSubmit(e) {
  	e.preventDefault()
  	setSearchPageVisible(true)
  	navigate('/search')
  }

	return(
		<div>
			<InstantSearch searchClient={searchClient} indexName="coverful">
				<div className="pastel8" style={{height: "2.535rem", color: "rgb(38,38,38)", display: "flex", justifyContent: "center", alignItems: "center", fontSize: 14}}>Enjoy free shipping on all UK orders! Shipping to rest of the world: £2.99
				</div>
				<Wrapper>
					<Container className="black-text">
						<div>
							<Logo to='/'>
								{siteTitle}
							</Logo>
							<MenuLink to='/cart'>
								SHOP
								<img alt="" style={{height: 12, position: "relative", top: 1, left: 1}} src={downArrow} />
							</MenuLink>
							<MenuLink to='/cart'>
								ABOUT US
							</MenuLink>
						</div>
						<div style={{width: 260}}>
							<div style={{textAlign: "center"}}>
								<SearchBox onSubmit={handleSubmit} onChange={handleChange} onFocus={handleFocus} onBlur={handleBlur}/>
							</div>
							<div className={searchActive && location !== '/search' ? "visible" : "hidden"} style={{width: 260, maxHeight: 360, overflow: "scroll", position: "absolute", top: 60, backgroundColor: "white", borderRadius: 0, boxShadow: "0px 0px 4px 0px rgba(0,0,0,0.2)"}}>
								<div style={{cursor: "pointer", textAlign: "center", padding: 14, fontFamily: "AvenirBold", fontSize: 14, backgroundColor: "rgb(246,246,246)", borderBottom: "1px solid rgb(220,220,220)"}} onClick={handleSubmit} className="hits-small">See all results for "{searchTerm}"</div>
								<Hits className="hits-small" hitComponent={SearchResult} />
							</div>
						</div>
						<MenuLink style={{position: "relative", display: "flex", justifyContent: "center", height: "100%", alignItems: "center", paddingRight: 0, width: 22}} to='/cart'>
							<img alt="basket" style={{height: 22, position: "absolute", marginLeft: 2}} src={hasItems ? blackBag : whiteBag} />
							{hasItems && <CartCounter>{quantity}</CartCounter>}
						</MenuLink>
					</Container>
				</Wrapper>
				{location === '/search' && searchPageVisible &&
					<SearchResultsWrapper>
						<h1>Search Results for "{searchTerm}"</h1>
						<Grid className="grid">
							<Hits hitComponent={SearchResultLarge} />
						</Grid>
					</SearchResultsWrapper>
				}
			</InstantSearch>
		</div>
	)
}

Navigation.propTypes = {
	siteTitle: PropTypes.string,
}

Navigation.defaultProps = {
	siteTitle: ``,
}

export default Navigation
